@import url('https://fonts.googleapis.com/css?family=ZCOOL+QingKe+HuangYou');

.Menu {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  background: rgba(0, 0,0, 0.8);

  &_title {
    color: white;
    font-family: 'ZCOOL QingKe HuangYou', cursive;
  }
  
  &_list {
    display: flex;
    flex-direction: row;
    list-style-type: none;

    &-item {
      display: flex;
      align-items: center;
      margin-right: 20px;

      &-link {
        text-decoration: none;
        color: white;
        font-family: 'ZCOOL QingKe HuangYou', cursive;
        font-size: 20px;
        opacity: 0.9;
        transition: all 0.1s;

        &:hover {
          opacity: 1;
        }
      }
    }
  }
}